import React, { Component } from 'react';
import BaseLayout from '../components/layouts/BaseLayout';
import BasePage from '../components/shared/BasePage';
import BlogsDetails from '../pages/blogDetails';
import { Container, Row, Col, Button, Modal, ModalHeader, ModalBody, ModalFooter, Form, FormGroup, Label, Input } from 'reactstrap';
import Link from 'next/link';
import moment from 'moment'
import BlogContent from  "./jsonData/blogContent.json"

class Blogs extends Component{
    // state = {
    //     modal: false,
    //     backdrop: false
    // }
    constructor(props) {
        super(props);
        this.state = {
            modal: false,
          backdrop: false
        };
        this.toggle = this.toggle.bind(this);
      }
    
      toggle() {
        this.setState({
          modal: !this.state.modal
        });
        return(
            <div>
                
                <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className} backdrop={this.state.backdrop}>
                  <ModalHeader toggle={this.toggle}></ModalHeader>
                  <ModalBody>
                    
                  </ModalBody>
                  <ModalFooter>
                    <Button color="primary" onClick={this.toggle}>Do Something</Button>{' '}
                    <Button color="secondary" onClick={this.toggle}>Cancel</Button>
                  </ModalFooter>
                </Modal>
            </div>
        )
      }

    //   showModal() {
    //       return(
              
    //       )
    //   }


    render(){
        return(
            <BaseLayout title="Syed Hussain -  Follow My Journey" headerType={'landing'} className="blog-listing-page">
                <div className="masthead" style={{"backgroundImage": "url('/static/images/home-bg.jpg')"}}>
                    <div className="overlay"></div>
                    <Container>
                    <div className="row">
                        <div className="col-lg-8 col-md-10 mx-auto">
                        <div className="site-heading">
                        <span className="subheading"><h3>**PAGE UNDER CONSTRUCTION**</h3></span>
                            <h1>My Blogs</h1>
                            <span className="subheading">Where I share my travelling and programming experience...</span>
                            
                        </div>
                        </div>
                    </div>
                    </Container>
                </div>
                <BasePage className="blog-body">
                    <Row>
                    <Col md="10" lg="8" className="mx-auto">
                        
                        <React.Fragment>
                            {
                                BlogContent.map((blog, index) => {
                                    return(
                                        <div key={index}  className="post-preview">
                                                <h2 OnClick={this.NavToBlog} className="post-title">
                                                    { blog.title }
                                                </h2>
                                                <h5 className="post-subtitle">
                                                    { blog.desc }
                                                </h5>
                                                <Button outline color="info" onClick={this.toggle(blog.title)}>Read More...</Button>
                                            <p className="post-meta">
                                                Posted by { blog.postedBy } { moment().format('LLLL') }
                                            </p>
                                            <div>
                                            
                                            
      </div>
                                            <hr></hr>
                                        </div>

                                    )
                                })
                            }
                        

                        </React.Fragment>
                    </Col>
                    </Row>
                    <footer>
                    <Container>

                    </Container>
                    </footer>
                </BasePage>
                </BaseLayout>
        )
    }
}

export default Blogs;